public class HiringVehicle extends Vehicle{
	
    private int regNum;
    private String keyStatus;
    double hireRatePerDay;
    
    public HiringVehicle(int regNum, float hireRatePerDay)
    {
        super();
        this.regNum = regNum;
        this.hireRatePerDay = hireRatePerDay;
        this.keyStatus ="available";
    }
    
    public String updateKeyStatus()
    {
        this.setKeyStatus("on trip");
        return this.getKeyStatus();
    }

    public String getKeyStatus() {
        return keyStatus;
    }

    public void setKeyStatus(String keyStatus) {
        this.keyStatus = keyStatus;
    }

    public void setRegNum(int regNum) {
        this.regNum = regNum;
    }
    
    public int getRegNum() {
        return regNum;
    }
    
    public double getHireRatePerDay() {
        return hireRatePerDay;
    }

    public void setHireRatePerDay(double hireRatePerDay) {
        this.hireRatePerDay = hireRatePerDay;
    }

}


