import { createRouter, createMemoryHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import IteratorPatternView from '../views/IteratorPatternView.vue'
import PartOneOutputView from '../views/PartOneOutput.vue'
import PartTwoOutputView from '../views/PartTwoOutput.vue'

const routes = [
  {
    path: '/',
    name: 'screen-one',
    component: HomeView
  },
  {
    path: '/screen-two',
    name: 'screen-two',
    component: PartOneOutputView
  },
  {
    path: '/screen-three',
    name: 'screen-three',
    component: IteratorPatternView
  },
  {
    path: '/screen-four',
    name: 'screen-four',
    component: PartTwoOutputView
  },
  {
    path: '/congratulations',
    name: 'congratulations',
    component: () => import('../views/CongratulationsPage.vue')
  }
]

const router = createRouter({
  history: createMemoryHistory(),
  routes
})

export default router
